import {
  CFormGroup,
  CInput,
  CLabel
} from '@coreui/react';
import React from 'react';

const FormInput = ({ label, name, placeholder, type, handleChange, ...otherProps }) => (
  <CFormGroup>
    {label ? (
      <CLabel htmlFor={name}>{label}</CLabel>
    ) : null}
      <CInput id={name} name={name} type={type} onChange={handleChange} {...otherProps} placeholder={placeholder} required="required" />
  </CFormGroup>
);

export default FormInput;
